import React, { Component } from 'react';
import { players } from './../jsons/players.json'

class NewPlayerForm extends Component {

  constructor () {
    super();
    this.state = {
      name: ''
    };
    this.handleValue = this.handleValue.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleValue(e){
    var nombre = e.target.value;
    nombre = nombre.charAt(0).toUpperCase() + nombre.slice(1);

    this.setState({
      name: nombre
    })


  }

  handleSubmit(e){
    e.preventDefault();
    const player = this.state;
    this.props.addingPlayer(player);
    this.setState({
      name: ''
    });

  }

  render(){
    var disabled;

    if(this.props.players.length >= 8){
      disabled = true;
      console.log(disabled);
    } else {
      disabled = false;
      console.log(disabled);
    }

    return (
      <div className="NewPlayerForm">
        <div className="row">
          <form className="col s12 m5" onSubmit={this.handleSubmit}>
            <h6>Agregue un nuevo jugador:</h6>
            <div className="input-field col s12">

              {disabled
                ? <input type="text" name="name" value="" disabled/>
                : <input type="text" name="name" value={this.state.name} onChange={this.handleValue}/>
              }

              <label htmlFor="name">Nombre</label>
            </div>
              {disabled
                ?<button type="submit" disabled className="btn-floating right btn-large waves-effect waves-light green accent-4"><i className="material-icons">+</i></button>
                :<button type="submit" className="btn-floating right btn-large waves-effect waves-light green accent-4"><i className="material-icons">+</i></button>
              }
          </form>
        </div>
      </div>
    )
  }
}

export default NewPlayerForm;
