import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { players } from './jsons/players.json'
import Sidebar from './components/Sidebar'
import NewPlayerForm from './components/NewPlayerForm'
import ViewPlayer from './components/dataPlayer'
import Navbar from './components/Navbar'

class App extends Component {
  constructor(){
    super();
    this.state = {
      players
    }
    this.addPlayer = this.addPlayer.bind(this);
    this.removePlayer = this.removePlayer.bind(this);
  }

  addPlayer(player){
    this.setState({
      players: [...this.state.players, player]
    })
  }

  removePlayer(index){
    var nombre = this.state.players.name;
    this.setState({
      players: this.state.players.filter((e,i) => {
        return i !== index;
      })
    })
  }

  render() {
    return (
      <div className="App">
      <Navbar />

      <div className="container">
        <div className="section">
          <NewPlayerForm  players={this.state.players} addingPlayer={this.addPlayer} />
          <ViewPlayer removePlayer={this.removePlayer} players={this.state.players}/>
        </div>
      </div>

      </div>
    );
  }
}

export default App;
